ul {
    margin-bottom: 0;
    padding-left: 0;
    list-style: none;
}

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
p {
    margin-top: 0;
}

button {
    padding: 0;
    border: 0;
    box-shadow: none;
    background-color: transparent;
    cursor: pointer;
    font-weight: 300;
    outline: none;
}